import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '@/store/index'
import axios from 'axios'

import BlogList from '@/views/blogList.vue'
import BlogDetail from '@/views/blogDetail'
import BlogAdd from '@/views/blogAdd'
import MsgList from '@/views/msgList'
import MsgDetail from '@/views/msgDetail'

Vue.use(VueRouter)

const routes = [{
    path: '/blogList',
    component: BlogList
}, {
    path: '/blogDetail/:id',
    name: 'BlogDetail',
    component: BlogDetail
}, {
    path: '/blogAdd',
    component: BlogAdd
}, {
    path: '/msgList',
    component: MsgList
}, {
    path: '/msgDetail',
    component: MsgDetail
}, {
    path: '*',
    redirect: '/blogList'
}]

const router = new VueRouter({
    mode: 'hash',
    base: process.env.BASE_URL,
    routes
})

// 全局路由拦截
router.beforeEach(async (to, from, next) => {
    // console.log(to.path, from.path, '全局路由守卫')
    // 检测是否已经登录，没有登录跳转到登录页面
    await axios({
        method: 'post',
        url: '/platform/login'
    }).then((res) => {
        if (res.data.status === 200) {
            store.dispatch('setDefaultSidebarAction', to.path)
            next()
        } else {
            window.location.href = '/admin/login'
        }
    })
})

export default router
